import * as Yup from 'yup';

export const validationPatientPageSchema = Yup.object().shape({
  email: Yup.string()
    .required(' `')
    .email('    '),
  password: Yup.string()
    .required(' `')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
  firstName: Yup.string()
    .required(' `')
    .max(64, '    64 '),
  lastName: Yup.string()
    .required(' `')
    .max(64, '    64 '),
  patronymic: Yup.string().max(64, '    64 '),
  phones: Yup.array().of(
    Yup.string()
      .required(' `')
      .max(13, '    13 ')
  ),
  contactMethod: Yup.string(),
});
