import SecondaryButton from 'components/SecondaryButton/SecondaryButton';
import {
  BtnBox,
  FormDescription,
  StyledForm,
  StyledPrimaryButton,
} from './Form.styled';
import PrimaryButton from 'components/PrimaryButton/PrimaryButton';

const Form = ({
  onSubmit,
  isRequiredFields,
  handlePublish,
  next,
  children,
  save,
  saveDraft,
  viewCard,
  sendRequest,
}) => {
  return (
    <StyledForm>
      <div>
        {isRequiredFields && (
          <FormDescription>
            <span>*</span> -  
          </FormDescription>
        )}
        {children}
      </div>

      <BtnBox>
        {viewCard && (
          <SecondaryButton disabled type="button">
               
          </SecondaryButton>
        )}
        {saveDraft && (
          <SecondaryButton type="submit"> </SecondaryButton>
        )}
        {(handlePublish || next) && (
          <PrimaryButton type="button" onClick={handlePublish || next}>
            {(handlePublish && '') ||
              (next && '') ||
              (save && '')}
          </PrimaryButton>
        )}
        {save && (
          <PrimaryButton type="submit" onClick={save}>
            
          </PrimaryButton>
        )}
        {sendRequest && (
          <StyledPrimaryButton type="submit" onClick={sendRequest}>
             
          </StyledPrimaryButton>
        )}
      </BtnBox>
    </StyledForm>
  );
};

export default Form;
